<template>
    <div class="main">
        <div class="topCover">
            <p class="titleName">XX的红包</p>
            <div class="img_icon">
                <img src="../../images/gift_icon_02.png" alt="">
            </div>
            <p>XX</p>
            <p style="font-size:.6rem;line-height:1rem">哈哈</p>
        </div>
        <div class="radius">
            <span class="open_pocket" @click="touch"><span>拆红包</span></span>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{}
    },
    methods:{
        touch(){
            // this.$vux.alert.show({
            //   title: '',
            //   content: '提示用',
            // })
            this.$router.push({path:'/shareRed'})
        }
    }
}
</script>

<style lang="less" scoped>
.main{
    height:100%;
    min-height: 16rem;
    background:#A44436;
    .topCover{
        color:#fff;
        height:55%;
        background:#BE4D3B;
        text-align: center;
        p{
            font-size:.5rem
        }
        .titleName{
            padding-top:30px;
            font-size:.8rem;
        }

        .img_icon{
            margin-top:40px;
            img{
                display: inline-block;
                width:3.2rem;
                height:3.2rem;
                border-radius: 50%;
            }
        }
    }
    .radius{
        height:10%;
        color:#fff;
        position:relative;
        background: url(../../images/radius.png) no-repeat ;
        background-size:100% 100%;
        text-align: center;
        vertical-align: middle;
        .open_pocket{
            width:4rem;
            height:4rem;
            line-height: 4rem;
            display: inline-block;
            position: absolute;
            bottom:-2rem;
            margin-left:-2rem;
            background: url(../../images/gift_icon_06.png) no-repeat;
            background-size:100% 100%;
        }
    }
    .open_pocket span{
        font-size: .6rem;

    }
}


</style>



